

.Laskin1 {
    background: rgba(0, 0, 0, 0.7);
    border: 1px solid #2c2c2c;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.3);
    max-width: 600px;
    color: white;
    padding: 1em;
    margin: auto;
}
.Laskin2 {
    background: rgba(0, 0, 0, 0.7);
    border: 1px solid #2c2c2c;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.3);
    max-width: 600px;
    color: white;
    padding: 1em;
    margin: auto;
    margin-top: 1em;
}


hr {
    border-top: 3px solid #bbb;
}

#tLaskin {
    background-color: #313131;
    text-align: start;
    border: 1px solid #000000;
    margin-top: 1em;
    border-collapse: collapse;
    max-width: 600px;
    width: 100%;
    color: white;
}

th {
    text-align: start;
}

.d2 {
    text-align: end;
    margin-top: 5px;
}

.d1 {
    text-align: start;
    display: grid;
    background-color: #313131;
    border: 1px solid #000000;
    margin: 1rem auto 0 auto;
    max-width: 600px;
    padding: 1em;
    grid-gap: 16px;
  }

label {
    grid-column: 1 / 2;
}
 
input, button {
    grid-column: 2 / 3;
}

@media only screen and (max-width: 600px) {
    .btn {
        background-color: #313131;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.3);
        color: white;
        padding: 1em;
        margin: 8px;
        width: 8em;
        font-size: 18px;
        
    }
    .Laskin1, .Laskin2 {
        float: none;
    }

    label {
        grid-column: 2 / 1;
    }
     
    input, button {
        grid-column: 1 / 3;
    }
  }
  
  

