.App {
    background-image: url(./tausta3.png);
    min-height: 100vh;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center; 
    background-size: cover;
    align-items: center;
    display: flex;
    flex-direction: column;
}


.wrapper {
  margin-top: 8em;
}

#wrap {
  justify-content: center;
  overflow:hidden;
  
  
}
#napit1 {
  float:left;
}
#napit2 {
  float:right;
}
#pnapit {
  margin-top: 0;
  margin-bottom: 0;
}


#kuvitus {
  margin-top: 1em;
  margin-left: 1em;
  margin-right: 2em;
  width: 40%;
  height: auto;
  float:right;
}

#somelink {
  height: 50px;
  padding: 10px
}

  
.btn {
    background-color: #313131;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.3);
    color: white;
    padding: 1em;
    margin: 8px;
    width: 10em;
    font-size: 20px;
    
}

.sivu {
  color: white;
  background-color:rgba(0, 0, 0, 0.7);
  margin: 3em;
  margin-top: 6em;
  padding: 2em;
}

@media only screen and (max-width: 1120px) {
  #kuvitus {
    width: 20em;
  }
  
  #napit1 { 
   float:none;
 }
 #napit2 { 
  float: none;
  
  }
  #kuvitus {
      display: none;
    }
}


@media only screen and (max-width: 600px) {
  .btn {
      background-color: #313131;
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.3);
      color: white;
      padding: 1em;
      margin: 8px;
      width: 8em;
      font-size: 18px;
      
  }
}

